import   React                              from 'react';
import                                           '../../style/bootstrap.min.css';

export class SubmitButton extends React.Component{
  render(){
    return(
      <div id='submit_part' className="form-group">
        <button type="submit" disabled={this.props.isSubmitting} className={(!this.props.errors.passportNumber&&!this.props.errors.email)?"btn btn-primary":"btn btn-danger"}>
          Отправить данные
        </button>
        {this.props.isSubmitting &&
          <img alt='loading' src="data:image/gif;base64," />
        }
      </div>
    )
  }
}

export default SubmitButton;